
import axios from "axios";
import wx from 'weixin-js-sdk';
import weixin from '@/services/weixin';

import XHeader from 'vux/src/components/x-header/index.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import Alert from 'vux/src/components/alert/index.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import DatetimePlugin from 'vux/src/plugins/datetime/index.js'
import Datetime from 'vux/src/components/datetime/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Picker from 'vux/src/components/picker/index.vue'
import InlineXNumber from 'vux/src/components/inline-x-number/index.vue'


export default {
components: {
XHeader,
GroupTitle,
Flexbox,
FlexboxItem,
XButton,
DatetimePlugin,
Datetime,
Group,
Picker,
InlineXNumber,
Alert,
},
data() {
return {
Messge13: "",
Messge14: "",
Messge15: "",
itemscore: 0,

picList15:{list:[],arr:[]},
picList14:{list:[],arr:[]},
picList13:{list:[],arr:[]}
};
},
computed: {
userName() {
return decodeURIComponent(this.$route.params.name);
}
},
methods: {
submit() {
let {departmentId, userId, partmentId} = this.$route.params;
let {Messge13, Messge14, Messge15, itemscore} = this;
let obj = {
Messge13,
Messge14,
Messge15,
itemscore,
departmentid: +departmentId,
userid: +userId,
partmentid: +partmentId,
};
if(this.picList13.arr.length!=0) {
obj.pic13 = this.picList13.arr.join(",");
}
if(this.picList14.arr.length!=0){
obj.pic14= this.picList14.arr.join(",");
}
if(this.picList15.arr.length!=0){
obj.pic15= this.picList15.arr.join(",");
}
axios.post("/dangjian/pavantgrade/save", obj).then(res => {
console.log(res.data);
if(res.suceess){
this.$vux.alert.show({title:'提交成功'});
}else {
this.$vux.alert.show({title:'提交失败'});
}
}).catch(function (error) {
console.log(error);
});
}
,

chooseImage(it) {
wx.chooseImage({
count: 1, // 默认9
sizeType: ['original', 'compressed'], // 可以指定是原图还是压缩图，默认二者都有
sourceType: ['album', 'camera'], // 可以指定来源是相册还是相机，默认二者都有
success: res => {
let localIds = res.localIds || [];
new Promise(resolve => {
let serverIds = [];
let toUpload = localId =>
wx.uploadImage({
localId: localId, // 需要上传的图片的本地ID，由chooseImage接口获得
isShowProgressTips: 1, // 默认为1，显示进度提示
success: res => {
serverIds.push(res.serverId);
if (localIds.length) {
toUpload(localIds.shift());
} else {
resolve(serverIds);
}
}
})
;
if (localIds.length) {
toUpload(localIds.shift());
} else {
resolve(serverIds);
}
}).
then(serverIds => {
let promiseList = [];
serverIds.map(serverId =>
promiseList.push(
this.$http.get('picture/upload', {
params: {
mediaId: serverId
}
})
)
)
;
Promise.all(promiseList).then(result => {
let pictureIds = [];
result.map(item => pictureIds.push(item.data)
)
;
it.list.push("http://www.dlbdata.cn/dangjian/picture/show?pictureId=" + pictureIds.join());
it.arr.push(pictureIds.join());
})
;
})
;
}
})
;
}
}
,
mounted() {
weixin.init(['chooseImage', 'uploadImage']);
}
};



